import { Language } from '@material-ui/icons'
import React from 'react'

function RunningRowingFR() {
    return (
        <div>
            <article>
                <p>
                J’ai décidé d’aller à la salle de musculation avec un ami. À la suite, il m’a annoncé qu’il serait en retard de 15 minutes.  Avec 15 minutes de plus, j’ai décidé de faire un peu de cardio avant de faire de la musculation. Donc, me voici dans la salle de sport avec le choix d’utiliser le rameur, le vélo immobile ou la machine à course. C’est à ce moment que je me suis demandé la question du titre, Qu’est qui est mieux entre la course à pied, pédaler et ramer.    
                </p>
                <p>
                Par chance pour moi, le rameur ou la course à pied était mes choix parce que je fais déjà trop souvent du vélo.  Mais, même avec mon nombre de choix réduit à 2 j’étais toujours indécis. Mon but dans la salle de musculation est de gagner plus de masse musculaire donc, j’ai cherché un article sur CIHNAL. CIHNAL est un moteur de recherche en ligne pour des articles qui sont écrit par des infirmiers, nutritionnistes, kinésiologie et autres professionnels de la santé. L’article que j’ai trouvé est un article rédigé par Edwards AM et Lander PJ en 2021 dans le journal de la médicine du sport, issue 52, volume 4 pages 344-350.                </p>
                <p>
                L’article décrit au-dessus est utile parce que sa contient de l’information qui est transférable et applicable à ma question. On parle de transférabilité lorsqu’un article est assez récent, lorsque la population étudier ressemble au votre et aussi lorsque l’information est validée par un autre rechercher. L’échéance pour un article à jour est souvent 10-15 ans. Parce que l’article a été rédigé en 2012 sa passe de justesse. La recherche a été faite avec une population anglaise qui est comparable à une population nord-américaine. Finalement les auteurs sont des professionnels de la santé et des sciences sportives. Patrick Edward Lander enseigne dans l’institut est de la technologie avec une maitrise en exercice alors que Andrew Mathew Edwards est un chercheur à l’université de Canterbury dans les sciences de la vie.                </p>
                <p>
                Dans leurs articles, les auteurs comparent la différence en VO2max, lactate sanguin et la moyenne du débit cardiaque entre les personnes qui rament et qui courent. Afin d’avoir ces résultats, ils ont demandé une dizaine de personnes aux alentours de 24 ans de courir et ramer pendant 20 minutes avec la même quantité d’effort.  Après la collecte de données ils se sont aperçus que courir avait un plus grand impact cardiorespiratoire que ramer. La moyenne cardiaque pour les personnes qui courraient était 174+/-5.9 battement/min contre 165+/-6.6 battements/minutes. La VO2max témoignait les mêmes résultats avec une VO2max de 86.1% contre 83.7% utilisé pendant l’effort physique. Finalement la concentration en lactate sanguin à influencer de la même manière. La concentration post-exercice étaient 4.0+/-0.8mmol/L contre 3.3+/-1.2mmol/L. Donc, qu’est ce que cela veut dire pour nous? Cela veut juste dire que courir à un meilleur impact cardiorespiratoire que ramer.                </p>
                <p>
                Maintenant est-ce que ces résultats devraient influencer votre choix d’activité cardio pré-musculation. Non, pas vraiment. Si vous voulez plus poussez vos poumons et votre cœur dans les 20 minutes de cardio vous pouvez courir parce que la différence n’est pas négligeable. Mais si votre but est comme moi de plus travailler les muscles alors la question se complexifie plus. Dans mon avis, ramer à un plus grand impact sur les muscles supérieurs que courir, mais c’est juste un avis. Si vous aviez aimer cette article revisité le site prochainement pour un autre article que je vais déchiffrer. 
                </p>
                <p>
                Mickael Thébaud. 
                </p>
            </article>
        </div>
    )
}

export default RunningRowingFR
